import React, { useCallback, useEffect, useState } from "react";
import { Avatar, Card, Button, notification } from "antd";


// import { useDispatch } from "react-redux";
// import { logoutAction } from "../reducers/user";
import { useSelector, useDispatch } from 'react-redux';
import { LOG_OUT_REQUEST } from '../reducers/user';


const dummy = {
    nickname: "",
    Post: [],
    Followings: [],
    Followers: [],
    isLoggedIn: false,
};


const UserProfile = ({ setIsLoggedIn }) => {
    const dispatch = useDispatch();
    const { me, logOutLoading, logInLoading, logInDone, logOutDone, logOutUser } = useSelector((state) => state.user);

    useEffect(() => {
        notification.open({
            message: '',
            description: " " + me.nickname + ""
        })
    }, [me && me.id && logInDone]);

    const onLogOut = useCallback(() => {
        dispatch({
            type: LOG_OUT_REQUEST,
        });

        notification.open({
            message: '',
            description: "  " + logOutUser + ""
        })

    }, [logOutDone]);

    return (
        <Card
            actions={[
                <div key="twit">
                    
                    <br />
                    {me.Posts.length}
                </div>,
                <div key="following">
                    
                    <br />
                    {me.Followings.length}
                </div>,
                <div key="follower">
                    
                    <br />
                    {me.Followers.length}
                </div>,
            ]}
        >
            <Card.Meta
                avatar={<Avatar>{me.nickname[0]}</Avatar>}
                title={me.nickname}
            />
            <Button onClick={onLogOut} loading={logOutLoading}></Button>
        </Card>
    );
};


export default UserProfile;